<template lang="jade">
.col-md-12
  .col-md-7
    table.table
      thead
        tr
          th #
          th 
          th 
          th 
          th 
          th 
      tbody
        tr(v-for='item, $index in coupon')
          td(style='color: #ff6600') {{$index + 1}}
          td {{item.minus}} 
          td {{item.counter}} 
          td {{item.limit}} 
          td {{item.due}}
          td
            span.icon delete
  .col-md-5
    .card
      .card-main
        .card-inner
          .form-group.form-group-label
            label.floating-label 
            input.form-control(type='text', v-model.number='data.minus')
          .form-group.form-group-label
            label.floating-label 
            input.form-control(type='text', v-model.number='data.counter')
          .form-group.form-group-label
            label.floating-label 
            input.form-control(type='text', v-model.number='data.limit')
          .form-group.form-group-label
            label.floating-label 
            input.form-control#selector(type='text', v-model='data.due')
          .card-action-btn.pull-left
            span.icon add
            a.btn.btn-flat.waves-attach.waves-effect(@click='add') 
</template>

<script>
export default {
  data () {
    return {
      data: {}
    }
  },
  computed: {
    coupon() {
      return this.$store.getters.coupon
    }
  },
  mounted () {
    var vm = this

    $('#selector').pickdate(
      {
        format: 'yyyy  mm  dd ', // escape any formatting characters with an exclamation mark
        formatSubmit: 'yyyy  mm  dd ',
        onClose(e) {
          let val = $('input[name="_submit"]').val()

          vm.data.due = val
        }
      }
    )

  },
  methods: {
    add() {
      let body = this.data

      if (!body) {
        return
      }

      let req = {
        model: 'coupon',
        body: body
      }

      this.$store.dispatch('create', req)
      this.data = {}
    },
  },
  components: {}
}
</script>

<style lang="less">
</style>



