<template lang="jade">
.col-md-8
  table.table
    thead
      tr
        th 
        th 
        th 
        th 
    tbody
      tr(v-for='item in users')
        td {{item.nickname}}
        td
          img.img-responsive(:src='item.headimgurl')
        td {{item.sex == '1' ? '' : ''}}
        td {{item.city}}

</template>

<script>
export default {
  data () {
    return {}
  },
  computed: {
    users() {
      return this.$store.getters.users
    }
  },
  mounted () {},
  methods: {},
  components: {}
}
</script>

<style lang="less">
</style>



